.navbar {
  display: flex;
  background-color: var(--purple);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  font-family: var(--fontfamily);
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3);
}

.navbar img {
  height: 50px;
  padding-left: 10px;
  padding-top: 5px;
}

#navbar-links {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  justify-content: flex-end;
  align-items: center;
  padding-right: 10px;
  margin: 0;
}

#navbar-links a,
#navbar-links a:visited {
  text-decoration: none;
  color: var(--offwhite);
}

#navbar-links li {
  padding: 0 15px;
  height: 45px;
  line-height: 45px;
  list-style-type: none;
  position: relative;
  display: inline-block;
  margin: 5px 10px;
  outline: none;
  color: var(--offwhite);
  text-decoration: none;
  letter-spacing: 1px;
  font-weight: 400;
  text-shadow: 0 0 1px rgba(255, 255, 255, 0.3);
  font-size: 0.75em;
}

#navbar-links li::before {
  top: 0;
  left: 0;
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  transform: rotate(90deg);
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  transform-origin: 0 0;
}

#navbar-links li::after {
  right: 0;
  bottom: 0;
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  transform: rotate(90deg);
  -webkit-transform-origin: 100% 0;
  -moz-transform-origin: 100% 0;
  transform-origin: 100% 0;
}

#navbar-links li::before,
#navbar-links li::after {
  position: absolute;
  width: 45px;
  height: 1px;
  background: var(--offwhite);
  content: '';
  opacity: 0.2;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
  pointer-events: none;
}

.navbar *,
.navbar *:after,
.navbar *::before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

#navbar-links li:hover::before,
#navbar-links li:hover::after,
#navbar-links li:focus::before,
#navbar-links li:focus::after {
  opacity: 1;
}

#navbar-links li:hover::before,
#navbar-links li:focus::before {
  left: 50%;
  -webkit-transform: rotate(0deg) translateX(-50%);
  -moz-transform: rotate(0deg) translateX(-50%);
  transform: rotate(0deg) translateX(-50%);
}

#navbar-links li:hover::after,
#navbar-links li:focus::after {
  right: 50%;
  -webkit-transform: rotate(0deg) translateX(50%);
  -moz-transform: rotate(0deg) translateX(50%);
  transform: rotate(0deg) translateX(50%);
}

#navbar-links li a.active {
  /* TODO: Make this work so that it looks like when it's hovered when on that page */
  /* color: var(--red); */
  right: 50%;
  -webkit-transform: rotate(0deg) translateX(50%);
  -moz-transform: rotate(0deg) translateX(50%);
  transform: rotate(0deg) translateX(50%);
}
