/* Mobile first approach. Responsive breakpoints are listed smallest to highest */
/* Small devices, Phones up to 575px */
body {
  margin-top: 55px;
  background-color: var(--darkgrey);
  font-family: var(--fontfamily);
  font-size: 20px;
}

.yamovie-movie-list {
  opacity: 0.3;
}


#yamovie-movie-list {
  display: flex;
  flex-direction: row;
  /* width: 100%; */
  flex-wrap: wrap;
  color: var(--offwhite);
  font-family: var(--fontfamily);
  justify-content: center;
}

#yamovie-movie-item {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 15px;
}

.img-fluid {
  max-width: 100%;
  height: auto;
  /* height: 400px;
  width: 275px; */
  box-shadow: 2px 2px 2px rgba(119, 117, 117, 0.2);
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  #yamovie-movie-item {
    width: 50%;
    padding-left: 10px;
    padding-right: 10px;
  }

  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

/* Medium devices (tablets portrait, 768px and up)  */
@media (min-width: 768px) {
  #yamovie-movie-item {
    width: 45%;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 15px;
  }

  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  #yamovie-movie-item {
    width: 28.333%;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 15px;
  }

  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  #yamovie-movie-item {
    width: 23%;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

#list-genres {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

#list-genres button {
  border: none;
  color: var(--offwhite);
  background: none;
  cursor: pointer;
  padding: 5px 5px;
  display: inline-block;
  text-transform: uppercase;
  font-weight: 700;
  outline: none;
  position: relative;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
  font-size: 1rem;
}

#list-genres button:after {
  content: '';
  position: absolute;
  z-index: -1;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
}

#list-genres button:hover {
  background-color: var(--lightgrey);
  transition: 180ms;
}

#list-all-movies {
  display: flex;
  flex-direction: row;
  width: 100%;
  flex-wrap: wrap;
  color: var(--offwhite);
  font-family: var(--fontfamily);
}